.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite turnover;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite turnover;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.stopwatch__middle-point {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

.stopwatch__seconds-hand .stopwatch__minutes-hand {
  animation-duration: 600s;
}

@keyframes turnover {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
